@font-face {
  font-family: 'Yeseva One';
  src: local('Yeseva One'), url('YesevaOne-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Avenir';
  src: local('Avenir'), url('Metropolis-Light.otf') format('opentype');
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f2f1e9;
}



h1 {
  font-size: 110px;
  font-family: "Yeseva One";
  font-weight: 1000;
}

h3 {
  font-family: "Yeseva One";
  color: white;
  font-size: 40px;
}

h2 {
  font-family: "Avenir";
  color: #519162;
  letter-spacing: .02em;
  font-weight: 400;

}

h4 {
  font-family: "Avenir";
  color: black;
  font-size: 120%;
  font-weight: 800;
  margin: 0;
}

h5{
  color: #7a877a;
  font-family: "Yeseva One";
  font-size: 110%;
  font-weight: 100;
  margin-top: 0%;
  margin-bottom: 0%;
}
p{
  margin: 0;
  font-family: "Avenir";
  margin-top: 0;
}
.all {
  width: 50%;
  height: 100%;
  margin: auto;
  align-content: center;
  margin-top: 10%;


}
.main-div {
  margin-top: 10%;
  align-items: left;
  text-align: left;

}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #42724e;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #243f2b;
}

hr{
  border: 1px solid #7a877a;
}
.card-div{

  display: inline-flex;
  text-align: center;
  align-items: center;
}

.fa-a {
  padding-right: 10px;
}

.resume-div {
  display: inline-flex;
  text-align: center;
  align-items: center;
}

.popover-class {
  width: 1000px;
  margin: 10px;
  height: 500px;
  text-align: left;
  overflow-y: scroll;
  white-space:pre-wrap;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.typewriter h1 {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  text-align: left;
  align-items: left;
  color: #3a3a3a;
  border-right: .15em solid orange; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 auto; /* Gives that scrolling effect as the typing happens */
  letter-spacing: .05em; /* Adjust as needed */
  width: 100%;
  animation:
    typing 1.5s steps(24, end),
    blink-caret .75s step-end infinite;
}

/* The typing effect */
@keyframes typing {
  from { width: 0 }
  to { width: 1000 }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: #3a3a3a; }
}
